.image{ 
    width: 12em;
    display: flex; 
    justify-content: space-evenly; 
    flex-wrap: wrap; 
    opacity: .3; 
}

.image:hover{ 
    box-shadow: 0 0 2px 1px rgba(0, 0, 0, 0.5); 
    filter: none; 
    opacity: .8;
}

.parent{ 
    position: relative; 
    text-align: center;
}



.imageButtons{ 
    position: absolute;
    bottom: 12%;  left: 12%; 
    opacity: 1; 
    display: inline-block;
    opacity: 1; 
}



.individualButton{ 
    color: rgb(201, 104, 104); 
    font-size: 13px; 
    width: 100%;  
    height: 100%; 
    opacity: .1; 
}
.individualButton:hover{ 
    opacity: .5
}
